<template>
  <div class="shopdetialitem">
    <div class="allcontent">
      <div class="shopchoose" :class="{checked: ischecked}" @click="checkedchange"></div>
      <div class="shopdetialcontent">
        <img :src="product.image" class="shopimage">
        <h3 class="shoptitle">{{product.title}}</h3>        
        <div class="shopprice">单价:￥{{product.price}}</div>
        <div class="shopcount">数量:{{product.count}}</div>
      </div>
    <div class="delateshop">X</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Shopdetialitem",
  props: {
    product: {
      type: Object,
      default() {
        return {};
      }
    },
    indexs: {
      type: Number
    }
  },
  data() {
    return {
      ischecked: true,      
    }
  },
  methods: {
    checkedchange() {
      this.ischecked = !this.ischecked;
      this.$store.state.cartList[this.indexs].checked = !this.$store.state.cartList[this.indexs].checked    
      //console.log(this.$store.state.cartList[this.indexs])
    }  
  }
};
</script>
<style>
.allcontent{
  width: 100%;
  height: 20vh;  
  padding-top: 15px;  
  position: relative;
  background-color: rgb(245, 234, 234);
}
.shopchoose {
  width: 20px;
  height: 20px;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  border-radius: 10px;
  border: 1px solid gray;
}
.checked {
  background-color: red;
}
.delateshop{
  position: absolute;
  top: 3vh;
  right: 3vh;
}

.shopdetialcontent {
  width: 85%;
  height: 20vh;
  background-color: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 1vh 1vh 1vh rgb(193, 193, 193);
  float: right;
  margin-right: 10px;  
}
.shopimage {
  width: 30%;
  height: 20vh;
  display: block;
  margin: 0;
  float: left;
  border-radius: 15px;
}
.shoptitle {
  height: 9vh;
  width: 31vh;
  font-size: 14px;
  text-align: center;
  float: left;
  overflow: hidden;
  margin-top: 3vh;
}
.shopprice{
position: absolute;
top: 17vh;
left: 23vh;
font-size: 17px;
font-weight: 500;
color: red;
}
.shopcount {
position: absolute;
top: 17vh;
right: 4vh;
font-size: 17px;
font-weight: 500;
color: blue;
}
</style>