<template>
  <div class="totalbar">   
    <div class="aaab"><!-- @click="allselectclick" 在下一行标签里添加-->
      <div class="totalcheck"  :class="{allchecked: isSelectall}"></div>
      <div class="aaass">全选</div>
    </div>

    <div class="totalprice">
      <span>总计价格: {{totalprice}}元</span>
    </div>
    <div class="payforall">
      <span>去结算({{checkLength}})</span>
    </div>
  </div>
</template>
<script>
import shopdetialitem from "./Shopdetialitem";
import { connect } from "tls";

export default {
  name: "Totalbar",
  components: {
    shopdetialitem
  },
  computed: {
    totalprice() {
      return this.$store.state.cartList
        .filter(item => {
          return item.checked;
        })
        .reduce((preValue, item) => {
          return preValue + item.price * item.count;
        }, 0)
        .toFixed(2);
    },
    checkLength() {
      return this.$store.state.cartList.filter(item => {
        return item.checked;
      }).length;
    },
    isSelectall() {
      if (this.$store.state.cartList.length == 0) {
        return false;
      } else {
        if (
          this.$store.state.cartList.filter(item => item.checked).length ==
          this.$store.state.cartList.length
        ) {
          return true;
        } else {
          return false;
        }
      }
    }
  },
  methods: {
    allselectclick() {
      //这里的功能实现不完整··全选的点击对子组件无影响，
      //我直接修改了store的数据但是子组件已经创建好了不会因为数据改变而变化，
      //我初步设想refresh一次子组件但是功能暂未实现
      if (this.isSelectall) {
        this.$store.state.cartList.forEach(item => {
          item.checked = false;
          return true;
        });
      } else {
        this.$store.state.cartList.forEach(item => {
          item.checked = true;
          return false;
        });
      }
    }
  }
};
</script>
<style>
.totalbar {
  height: 44px;
  width: 100%;
  position: fixed;
  background-color: #eee;
  bottom: 49px;
}
.totalcheck {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 11px;
  left: 16px;
  border: 1px solid gray;
  border-radius: 10px;
}
.aaass {
  position: absolute;
  top: 13px;
  left: 43px;
  font-size: 15px;
  font-weight: 600;
}
.aaab {
  float: left;
  height: 44px;
  width: 90px;
}
.totalprice {
  float: left;
  height: 44px;
  line-height: 44px;
  font-weight: 500;
  font-size: 16px;
}
.payforall {
  background-color: red;
  height: 44px;
  width: 120px;
  float: right;
  font-size: 18px;
  text-align: center;
  line-height: 44px;
  font-weight: 600;
  color: white;
}
.allchecked {
  background-color: red;
}
</style>